/* eslint-disable react/no-danger */
// @flow

import React from 'react'
import {Link, graphql} from 'gatsby'
import styled from 'styled-components'
import {Typography} from '@material-ui/core'
import {OutboundLink} from 'gatsby-plugin-google-gtag'
import {Disqus} from 'gatsby-plugin-disqus'

import PostHeader from './PostHeader'

import Layout from '../Layout'
import SEO from '../SEO'

type Props = {
  data: {
    markdownRemark: {|
      id: string,
      frontmatter: {|
        title: string,
        date: string,
        description: string,
        coverImage: {|
          src: string,
          owner: {|
            name: string,
            link: string,
          |},
          platform: {|
            name: string,
            link: string,
          |},
        |},
      |},
      excerpt: any,
      html: string,
    |},
    site: {
      siteMetadata: {|
        title: string,
        author: {|
          name: string,
        |},
        social: {|
          twitter: string,
          facebook: string,
          github: string,
        |},
        siteUrl: string,
      |},
    },
  },
  pageContext: {
    slug: string,
    previous: {
      fields: {
        slug: string,
      },
      frontmatter: {
        title: string,
      },
    },
    next: {
      fields: {
        slug: string,
      },
      frontmatter: {
        title: string,
      },
    },
  },
  location: Location,
}

// const FONT_NANUM_GOTHIC: string = 'Nanum Gothic'
// const FONT_NANUM_GOTHIC_CODING: string = 'Nanum Gothic Coding'

const MyArticle = styled.article`
  max-width: ${props => props.theme.spacing(84)}px;
  margin-left: auto;
  margin-right: auto;
  font-family: Georgia, Cambria, 'Times New Roman', Times, serif;
  font-size: 15px;
`

const HR = styled.hr``
// /* margin-bottom: ${rhythm(1)}; */

const UL = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0px;
`

const MySection = styled.section`
  img {
    max-width: 100%;
  }

  p {
    @media screen and (max-width: ${props => props.theme.spacing(84)}px) {
      margin-top: 1.56em;
      font-size: 18px;
    }

    @media screen and (min-width: ${props => props.theme.spacing(125)}px) {
      margin-top: 2em;
      font-size: 21px;
    }
  }

  a {
    color: inherit;
  }
`

const Img = styled.img`
  max-width: 100%;

  @media screen and (max-width: ${props => props.theme.spacing(84)}px) {
    margin-top: ${props => props.theme.spacing(5)}px;
  }

  @media screen and (min-width: ${props => props.theme.spacing(125)}px) {
    margin-top: ${props => props.theme.spacing(7)}px;
  }
`

const ImgCaption = styled(Typography)`
  margin-top: 10px;
  text-align: center;
  font-size: 13px;
  line-height: 20px;
`

const MyLink = styled(OutboundLink)`
  /* text-decoration: none; */
  color: inherit;
`

const Footer = styled.footer`
  @media screen and (max-width: ${props => props.theme.spacing(84)}px) {
    margin-top: 3em;
  }

  @media screen and (min-width: ${props => props.theme.spacing(125)}px) {
    margin-top: 4em;
  }
`

const Post = (props: Props) => {
  const {data, pageContext, location} = props

  const {
    markdownRemark: {
      frontmatter: {
        title,
        date,
        coverImage: {
          src,
          owner: {name: coverImageOwnerName, link: coverImageOwnerLink},
          platform: {name: coverImagePlatformName, link: coverImagePlatformLink},
        },
      },
      excerpt,
      html,
    },
    site: {
      siteMetadata: {
        title: siteTitle,
        author: {name},
        siteUrl,
      },
    },
  } = data

  const {previous, next, slug} = pageContext

  const disqusConfig = {
    url: `${siteUrl}${slug}`,
    identifier: `${siteUrl}${slug}`,
    title,
  }

  return (
    <Layout location={location} title={siteTitle} posts={[]}>
      <SEO title={title} description={excerpt} />
      <MyArticle>
        <PostHeader title={title} date={date} author={name} />
        <Img src={src} alt={`Photo by ${coverImageOwnerName} on ${coverImagePlatformName}`} />
        <ImgCaption variant="body2">
          Photo by <MyLink href={coverImageOwnerLink}>{coverImageOwnerName}</MyLink> on{' '}
          <MyLink href={coverImagePlatformLink}>{coverImagePlatformName}</MyLink>
        </ImgCaption>
        <MySection dangerouslySetInnerHTML={{__html: html}} />
        <HR />
        <Footer>
          <Disqus config={disqusConfig} />
        </Footer>
      </MyArticle>

      <nav>
        <UL>
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                 {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} 
              </Link>
            )}
          </li>
        </UL>
      </nav>
    </Layout>
  )
}

export default Post

export const pageQuery = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
        author {
          name
        }
        social {
          twitter
          facebook
          github
        }
        siteUrl
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD A hh:mm:ss")
        coverImage {
          src
          owner {
            name
            link
          }
          platform {
            name
            link
          }
        }
      }
    }
  }
`
